<template>
    <div class="first-layout" style="flex-direction: column;">
        <div class="text-content default-border">
            <Topic v-for="(item, i) in topics" :key="item.question" :index="i" :topic="item">
                <hr v-if="i !== 7">
            </Topic>
        </div>
        <button class="submit-answer" @click="submit">提交</button>
        <div v-if="show" class="problem-tooltip">
            <div class="default-text default-border dialog">
                <div class="header">
                    <button @click.self="closeTooltip">关闭</button>
                </div>
                <div>
                    <p>{{ tooltip }}</p>
                    <p v-if="errors.length > 0">错误: {{ errors.join('、') }}</p>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import Topic from "./Topic";
//实验习题
export default {
    name: "Problem",
    components: { Topic },
    created() {
        this.topics = this.$store.getters.GetRandomTopic();
    },
    data() {
        return {
            //题目
            topics: [],
            show: false,
            errors: [],
            tooltip: '',
        };
    },
    methods: {
        submit() {
            let score = 0;
            this.topics.forEach((topic, index) => {
                if (topic.select === topic.right) {
                    score += 12.5;
                }
                else {
                    this.errors.push(index + 1);
                }
            });
            if (score >= 80) {
                this.$store.commit('setShowEXP', true);
                this.$store.commit('setShowResult', true);
            }
            this.tooltip = `本次答题分数: ${score}  ${score >= 80 ? '通过' : '不通过'}`;
            this.show = true;
        },
        closeTooltip() {
            this.show = false;
            this.tooltip = '';
            this.errors = [];
        }
    }
};
</script>